<template>
  <div id="example-2">
    <div class="card">
      <ul class="quick-nav">
        <li><router-link :to="{ name: 'Nowintheater'}">影院热映</router-link></li>
        <li><a href="#">华语新碟帮</a></li>
        <li><a href="#">豆瓣时间</a></li>
        <li><a href="#">使用豆瓣</a></li>
      </ul>
    </div>
    <section id="recommend-feed">
      <div class="feed-section">
        <div v-for="(item, index) in arrList" class="feed-item" href="#">
          <router-link  :to="{ name: 'Group'}">
            <div class="feed-content">
              <div v-if="item.target.cover_url.length" >
                <div v-if="item.target.more_pic_urls.length">
                  <div class="photos">
                    <div class="main" v-bind:style="{backgroundImage: 'url(' +item.target.cover_url+ ')'}" style="position: relative; background:center center / cover no-repeat rgb(250, 250, 250);">
                     <div></div>
                    </div>
                    <div class="aside">
                      <div class="aside-pic">
                        <div v-bind:style="{backgroundImage: 'url(' +item.target.more_pic_urls[0]+ ')'}" style="position: relative; background:center center / cover no-repeat rgb(250, 250, 250);">
                          <div style="padding-top: 100%;"></div>
                        </div>
                      </div>
                    <div class="aside-pic">
                      <div v-bind:style="{backgroundImage: 'url(' +item.target.more_pic_urls[1]+ ')'}" style="position: relative; background:center center / cover no-repeat rgb(250, 250, 250);">
                        <div style="padding-top: 100%;"></div>
                      </div>
                      <div class="more-pic"><span class="count">{{item.target.photos_count-3}}+</span></div>
                    </div>
                    </div>
                  </div>
                  <h3>{{item.title}}</h3>
                </div>
                <div v-else>
                  <div v-if="item.target.cover_url.length">
                    <div class="cover" v-bind:style="{backgroundImage: 'url(' +item.target.cover_url+ ')'}" style="position: relative;background:center center / cover no-repeat rgb(250, 250, 250);"  >
                      <div style="padding-top: 100%;"></div>
                    </div>
                    <h3>{{item.title}}</h3>
                    <p>{{item.target.desc}}</p>
                  </div>
                  <div v-else>
                    <h3>{{item.title}}</h3>
                    <p>{{item.target.desc}}</p>
                  </div>
                </div>
              </div>
              <div v-else>
                <h3>{{item.title}}</h3>
                <p>{{item.target.desc}}</p>
              </div>
            </div>
            <div class="author">
              by&nbsp;
              <span>{{item.target.author.name}}</span>
            </div>
          <span class="feed-label">

          </span>
          </router-link>
        </div>

      </div>
    </section>
  </div>



</template>
<script>
  import $ from 'jquery'
export default {
  data(){
      return{
        arrList:[]
      }
  },
  mounted(){
    this.getData();
  },
  methods:{
    getData () {
      var _this = this
      $.ajax({
        url: 'https://m.douban.com/rexxar/api/v2/recommend_feed?alt=json&next_date=&loc_id=108288&gender=&birthday=&udid=9fcefbf2acf1dfc991054ac40ca5114be7cd092f&for_mobile=1',
        type: 'GET',
        dataType: 'JSONP',
        success: function (res) {
          _this.arrList=res.recommend_feeds
          console.log(_this.arrList)
        }
      })
    }
  }
}

</script>


<style scoped>
  #example-2{
    flex: 1;
    flex-direction: column;
    overflow: auto;
  }
  .card{
    margin: 0 .18rem;
  }
  .card:after{
    content: '';
    display: block;
    clear:both;
  }
  .quick-nav{
    margin-top: 20px;
  }
  .quick-nav>li{
    float: left;
    width: 50%;
    padding: 0.03rem;
    box-sizing: border-box;
    font-size: .15rem;
    text-align: center;
  }
  .quick-nav>li>a{
    display: block;
    background-color: #f6f6f6;
    color:#494949;
    line-height: 0.2rem;
    padding: .12rem 0;
    border-radius: 2px;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-wrap: normal;
  }
  #recommend-feed .feed-item {
    display: block;
    padding: 25px 18px 25px 0;
    margin-left: 18px;
    position: relative;
    color: #494949;
  }
  #recommend-feed .feed-content .cover {
    width: 25.6%;
    margin-left: 25px;
    float: right;
  }
  section p, section h3 {
    line-height: 22px;
    word-wrap: break-word;
    margin: 0;
    padding: 0;
  }
  #recommend-feed .feed-content h3 {
    text-align: justify;
    font-size: 17px;
    font-weight: 500;
    line-height: 1.41;
    color: #494949;
    margin-bottom: 6px;
  }
  #recommend-feed .feed-content p {
    overflow: hidden;
    text-align: justify;
    color: #aaa;
    font-size: 12px;
    line-height: 1.5;
    display: -webkit-box;
    overflow: hidden;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
  }
  #recommend-feed .feed-item .author {
    font-size: 12px;
    color: #ccc;
  }
  #recommend-feed .feed-content .photos {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 10px;
  }
  #recommend-feed .feed-content .photos .main {
    -webkit-box-flex: 3.2;
    -webkit-flex: 3.2;
    -ms-flex: 3.2;
    flex: 3.2;
    margin-right: 4px;
  }
  #recommend-feed .feed-content .photos .aside {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
  }
  #recommend-feed .feed-content .photos .aside .aside-pic {
    position: relative;
  }
  #recommend-feed .feed-content .photos .aside .aside-pic ~ .aside-pic {
    margin-top: 4px;
  }
  #recommend-feed .feed-content .photos .more-pic {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.4);
    color: #fff;
    text-align: center;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
  }
  #recommend-feed .feed-content .photos .more-pic .count {
    width: 100%;
  }
</style>



