<template>
  <div id="content">
		这是Books内容
	</div>
</template>

<script>
  export default{
    name:"content",
    data(){
      return{
        arrList:[]
      }
    },
    mounted(){
      this.fetchData();
    },
    methods: {
      fetchData(){
//        将所请求的数据放在static文件夹中
        var _this = this
        this.$http.get("static/data/home.data").then(function (res) {
          _this.arrList = res.data
        }).catch(function (error) {
          console.log(error);
        });
      }
    }
  }
</script>

<style scoped>
#content{
  width: 100%;
}
</style>



